        sections: {},
        wrapperConfig: { enable: true, tag: "google-analytics", attributes: {} },
        __props__: ["__WRAPPER_ELEMENT__", "createHtml", "__REFS__", "parseRefs"],
            __WRAPPER_ELEMENT__: __WRAPPER_ELEMENT__,
            refs: __REFS__,
            states: __STATE__,
            parseRefs: parseRefs,
            createHtml: createHtml,
        hasAwaitData: false,
        hasFetchData: false,
        subscribe: ["test", "testop", "isUseGoogleAnalytics"],
        fetch: null,
        data: $$$DATA$$$,
        viewId: __VIEW_ID__,
