export function TemplatesTest($$$DATA$$$ = {}, systemData = {}) {
    const {App, View} = systemData;
    const __VIEW_PATH__ = 'templates.test';
    const __VIEW_ID__ = $$$DATA$$$.__SSR_VIEW_ID__ || App.View.generateViewId();
    const __VIEW_TYPE__ = 'view';
    // this is the wrapper element
    
    const __WRAPPER_ELEMENT__ = document.createElement('template');
    const __REFS__ = [];
    const self = new View.Engine();
    const __STATE__ = new View.State(self);


    const parseRefs = (frag) => {
        for (let i = 0; i < frag.childNodes.length; i++) {
            const node = frag.childNodes[i];
            __REFS__.push(node);
        }
    }
    const createHtml = (template) => {
        try {
            __WRAPPER_ELEMENT__.innerHTML = template;
        } catch (error) {
            console.error(error);
            __WRAPPER_ELEMENT__.innerHTML = '';
        }
        let frag = __WRAPPER_ELEMENT__.content;
        parseRefs(frag);
        return frag;
    }
    
    /**
     * 
     * @param {*} value 
     * @returns {[any, function, string]}
     */
    const useState = (value) => {
        return __STATE__.__useState(value);
    };
    const updateRealState = (state) => {
        __STATE__.__updateRealState(state);
    };

    const lockUpdateRealState = () => {
        __STATE__.__lockUpdateRealState();
    };
    const updateStateByKey = (key, state) => {
        __STATE__.__updateStateByKey(key, state);
    };

    if(typeof $$$DATA$$$.__SSR_VIEW_ID__ !== 'undefined'){
        delete $$$DATA$$$.__SSR_VIEW_ID__;
    }
    const __UPDATE_DATA_TRAIT__ = {};
    const __VARIABLE_LIST__ = [];
    const $setTest = __STATE__.__register('test');
    let test = null;
    const setTest = (state) => {
        test = state;
        $setTest(state);
    };
    const update$SetTest = (value) => updateStateByKey('test', value);
    const $setDemo = __STATE__.__register('demo');
    let demo = null;
    const setDemo = (state) => {
        demo = state;
        $setDemo(state);
    };
    const update$SetDemo = (value) => updateStateByKey('demo', value);
    const $setClickCount = __STATE__.__register('clickCount');
    let clickCount = null;
    const setClickCount = (state) => {
        clickCount = state;
        $setClickCount(state);
    };
    const update$SetClickCount = (value) => updateStateByKey('clickCount', value);

    self.setup('templates.test', {
        superView: null,
        hasSuperView: false,
        viewType: 'view',
        sections: {},
        wrapperConfig: { enable: true, tag: "test", attributes: {} },
        __props__: ["__WRAPPER_ELEMENT__", "createHtml", "__REFS__", "parseRefs"],
            __WRAPPER_ELEMENT__: __WRAPPER_ELEMENT__,
            refs: __REFS__,
            states: __STATE__,
            parseRefs: parseRefs,
            createHtml: createHtml,
        hasAwaitData: false,
        hasFetchData: false,
        subscribe: ["test", "demo"],
        fetch: null,
        data: $$$DATA$$$,
        viewId: __VIEW_ID__,
        path: __VIEW_PATH__,
        usesVars: false,
        hasSections: false,
        hasSectionPreload: false,
        hasPrerender: false,
        renderLongSections: [],
        renderSections: [],
        prerenderSections: [],
        userDefined: {
            mounted() {
                console.log('Test template mounted');
            },
            clickMe() {
                setClickCount(clickCount + 1);
            }
        },
        resources: [],
        commitConstructorData: function() {
            // Then update states from data
            update$SetTest('test');
            update$SetDemo('demo');
            update$SetClickCount(0);
            // Finally lock state updates
            lockUpdateRealState();
        },
        updateVariableData: function(data) {
            // Update all variables first
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.updateVariableItem(key, data[key]);
                }
            }
            // Then update states from data
            update$SetTest('test');
            update$SetDemo('demo');
            update$SetClickCount(0);
            // Finally lock state updates
            lockUpdateRealState();
        },
        updateVariableItem: function(key, value) {
            this.data[key] = value;
            if (typeof __UPDATE_DATA_TRAIT__[key] === "function") {
                __UPDATE_DATA_TRAIT__[key](value);
            }
        },
        loadServerData: function() {
    
},
        prerender: function() {
    return null;
},
        virtualPrerender: function() {
    return null;
},
        render: function() {
                
    let __outputRenderedContent__ = '';
            try {
                __outputRenderedContent__ = `<h1>Test</h1>
<p>Test: ${App.View.escString(test)}</p>
<p>Demo: ${App.View.escString(demo)}</p>
<button >Set Test</button>
<button >Set Demo</button>
`;
            } catch(e) {
                __outputRenderedContent__ = this.__showError(e.message);
                console.warn(e);
            }
            return __outputRenderedContent__;
            },
        virtualRender: function() {

    let __outputRenderedContent__ = '';
    try {
        __outputRenderedContent__ = `<h1>Test</h1>
<p>Test: ${App.View.escString(test)}</p>
<p>Demo: ${App.View.escString(demo)}</p>
<button >Set Test</button>
<button >Set Demo</button>
`;
    } catch(e) {
        __outputRenderedContent__ = this.__showErrorScan(e.message);
        console.warn(e);
    }
    return this.__extendsScan('None');
},
        init: function() {  },
        destroy: function() {},
        addCSS: function() {
            // No CSS content found in @onInit
        },
        removeCSS: function() {
            const styles = document.querySelectorAll('style[data-view-name="templates.test"]');
            styles.forEach(style => style.remove());
        }

    });
    return self;
        }