            refs: __REFS__,
            states: __STATE__,
            parseRefs: parseRefs,
            createHtml: createHtml,
        hasAwaitData: false,
        hasFetchData: false,
        subscribe: ["should be different"],
        fetch: null,
        data: $$$DATA$$$,
        viewId: __VIEW_ID__,
        path: __VIEW_PATH__,
        usesVars: false,
        hasSections: true,
        hasSectionPreload: false,
        hasPrerender: false,
        renderLongSections: [],
        renderSections: ["title"],
        prerenderSections: [],
        userDefined: {},
        resources: [],
        commitConstructorData: function() {
            // Then update states from data
            
            // Finally lock state updates
            
        },
        updateVariableData: function(data) {
            // Update all variables first
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.updateVariableItem(key, data[key]);
                }
            }
            // Then update states from data
            
            // Finally lock state updates
            
        },
        updateVariableItem: function(key, value) {
            this.data[key] = value;
            if (typeof __UPDATE_DATA_TRAIT__[key] === "function") {
                __UPDATE_DATA_TRAIT__[key](value);
            }
        },
        loadServerData: function() {
    
},
        prerender: function() {
    return null;
},
        virtualPrerender: function() {
    return null;
},
        render: function() {
                
    let __outputRenderedContent__ = '';
            try {
                __outputRenderedContent__ = `

${this.__section('title', 'Test @onyield Directive Fixed', 'string')}

<div class="container">
<h1>Test @onyield Directive - Fixed Version</h1>

<div class="test-section">
<h2>1. Basic @onyield - Single Parameter</h2>
<div @onyield($userState)>Basic onyield test</div>
<!-- Expected: onyield-userState="#all" -->
</div>

<div class="test-section">
<h2>2. @onyield with Two Parameters</h2>
<div @onyield($classState, 'class')>Onyield with class</div>
<!-- Expected: onyield-classState="class" -->
</div>

<div class="test-section">
<h2>3. @onyield with Array Syntax</h2>
<div class="${App.View.yieldContent('classState', null)}" id="${App.View.yieldContent('idState', null)}" data-yield-attr="class:classState,id:idState">Array syntax</div>
<!-- Expected: onyield-classState="class" onyield-idState="id" -->
</div>

<div class="test-section">
<h2>4. @onyield with Special Keys</h2>
<div data-yield-content="contentState" data-yield-children="childrenState">Special keys</div>
<!-- Expected: onyield-contentState="#content" onyield-childrenState="#children" -->
</div>

<div class="test-section">
<h2>5. Multiple @onyield on Same Element</h2>
<div @onyield($titleState) @onyield($contentState, 'data-content')>Multiple onyield</div>
<!-- Expected: onyield-titleState="#all" onyield-contentState="data-content" -->
</div>

<div class="test-section">
<h2>6. Compare with </h2>
<div >Subscribe directive</div>
<div @onyield($userState)>Onyield directive</div>
<!-- Subscribe should generate PHP code, Onyield should generate static attributes -->
</div>

<div class="test-section">
<h2>7. Complex Array @onyield</h2>
<div @onyield([
'class' => $classState,
'data-id' => $idState,
'data-value' => $valueState,
'#content' => $contentState,
'#children' => $childrenState
])>Complex array</div>
<!-- Expected: onyield-classState="class" onyield-idState="data-id" onyield-valueState="data-value" onyield-contentState="#content" onyield-childrenState="#children" -->
</div>

<div class="test-section">
<h2>8. Alternative @onyield Names</h2>
<div @onYield($state1)>onYield</div>
<div @yieldOn($state2)>yieldOn</div>
<div @yieldon($state3)>yieldon</div>
<div @yieldListen($state4)>yieldListen</div>
<div @yieldlisten($state5)>yieldlisten</div>
<div @yieldWatch($state6)>yieldWatch</div>
<div @yieldwatch($state7)>yieldwatch</div>
<!-- All should generate same format: onyield-{state}="#all" -->
</div>

<div class="test-section">
<h2>Generated HTML Output:</h2>
<pre id="output"></pre>
</div>
</div>

<script>
document.addEventListener('DOMContentLoaded', function() {
// Display the generated HTML
const output = document.getElementById('output');
output.textContent = document.documentElement.outerHTML;
});
</script>
@block`;
            } catch(e) {
                __outputRenderedContent__ = this.__showError(e.message);
                console.warn(e);
            }
            return this.__extends('layouts.app');
            },
