export function CustomTest($$$DATA$$$ = {}, systemData = {}) {
    const {App, View} = systemData;
    const __VIEW_PATH__ = 'custom.test';
    const __VIEW_ID__ = $$$DATA$$$.__SSR_VIEW_ID__ || App.View.generateViewId();
    const __VIEW_TYPE__ = 'view';
    // this is the wrapper element
    
    const __WRAPPER_ELEMENT__ = document.createElement('template');
    const __REFS__ = [];
    const self = new View.Engine();
    const __STATE__ = new View.State(self);


    const parseRefs = (frag) => {
        for (let i = 0; i < frag.childNodes.length; i++) {
            const node = frag.childNodes[i];
            __REFS__.push(node);
        }
    }
    const createHtml = (template) => {
        try {
            __WRAPPER_ELEMENT__.innerHTML = template;
        } catch (error) {
            console.error(error);
            __WRAPPER_ELEMENT__.innerHTML = '';
        }
        let frag = __WRAPPER_ELEMENT__.content;
        parseRefs(frag);
        return frag;
    }
    
    /**
     * 
     * @param {*} value 
     * @returns {[any, function, string]}
     */
    const useState = (value) => {
        return __STATE__.__useState(value);
    };
    const updateRealState = (state) => {
        __STATE__.__updateRealState(state);
    };

    const lockUpdateRealState = () => {
        __STATE__.__lockUpdateRealState();
    };
    const updateStateByKey = (key, state) => {
        __STATE__.__updateStateByKey(key, state);
    };

    if(typeof $$$DATA$$$.__SSR_VIEW_ID__ !== 'undefined'){
        delete $$$DATA$$$.__SSR_VIEW_ID__;
    }
    const __UPDATE_DATA_TRAIT__ = {};
    let {user = {"name": "John", "email": "john@example.com"}, ipAddress = '127.0.0.1'} = $$$DATA$$$;
    const {name, email} = user;
    const test = null;
    const {title, description} = config;
    let unassigned;
    __UPDATE_DATA_TRAIT__.user = value => user = value;
    __UPDATE_DATA_TRAIT__.ipAddress = value => ipAddress = value;
    __UPDATE_DATA_TRAIT__.unassigned = value => unassigned = value;
    const __VARIABLE_LIST__ = ["user", "ipAddress", "unassigned"];
    const $setUserState = __STATE__.__register('userState');
    let userState = null;
    const setUserState = (state) => {
        userState = state;
        $setUserState(state);
    };
    const update$SetUserState = (value) => updateStateByKey('userState', value);
    const $setChartState = __STATE__.__register('chartState');
    let chartState = null;
    const setChartState = (state) => {
        chartState = state;
        $setChartState(state);
    };
    const update$SetChartState = (value) => updateStateByKey('chartState', value);

    self.setup('custom.test', {
        superView: 'layouts.base',
        hasSuperView: true,
        viewType: 'view',
        sections: {
        "document.body":{
            "type":"long",
            "preloader":false,
            "useVars":true,
            "script":{}
        }
    },
        wrapperConfig: { enable: false, tag: null, follow: true, attributes: {} },
        __props__: ["__WRAPPER_ELEMENT__", "createHtml", "__REFS__", "parseRefs"],
            __WRAPPER_ELEMENT__: __WRAPPER_ELEMENT__,
            refs: __REFS__,
            states: __STATE__,
            parseRefs: parseRefs,
            createHtml: createHtml,
        hasAwaitData: false,
        hasFetchData: false,
        subscribe: true,
        fetch: null,
        data: $$$DATA$$$,
        viewId: __VIEW_ID__,
        path: __VIEW_PATH__,
        usesVars: true,
        hasSections: true,
        hasSectionPreload: false,
        hasPrerender: false,
        renderLongSections: ["document.body"],
        renderSections: ["document.body"],
        prerenderSections: [],
        userDefined: {
mounted() {
console.log('View mounted');
},
submitForm(event) {
event.preventDefault();
console.log('Submit form', event);
setLoading(true);
},
showModal(event, modalId, checked, title, data) {
console.log('Show modal', event, modalId, checked, title, data);
},
updateField(event, fieldName) {
console.log('Update field', event, fieldName);
}
},
        resources: [],
        commitConstructorData: function() {
            // Then update states from data
            update$SetUserState(user);
            update$SetChartState(chartData);
            // Finally lock state updates
            lockUpdateRealState();
        },
        updateVariableData: function(data) {
            // Update all variables first
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.updateVariableItem(key, data[key]);
                }
            }
            // Then update states from data
            update$SetUserState(user);
            update$SetChartState(chartData);
            // Finally lock state updates
            lockUpdateRealState();
        },
        updateVariableItem: function(key, value) {
            this.data[key] = value;
            if (typeof __UPDATE_DATA_TRAIT__[key] === "function") {
                __UPDATE_DATA_TRAIT__[key](value);
            }
        },
        loadServerData: function() {
    
},
        prerender: function() {
    return null;
},
        virtualPrerender: function() {
    return null;
},
        render: function() {
                
    let __outputRenderedContent__ = '';
            try {
                __outputRenderedContent__ = `

${this.__section('document.body', `<div class="container">
<h1>Test @let and @const Directives</h1>

<!-- Test simple variables -->


<p>User: ${App.View.escString(App.Helper.json_encode(user))}</p>
<p>IP: ${App.View.escString(ipAddress)}</p>

<!-- Test array destructuring -->

<p>User State: ${App.View.escString(App.Helper.json_encode(userState))}</p>


<!-- Test object destructuring -->

<p>Name: ${App.View.escString(name)}</p>
<p>Email: ${App.View.escString(email)}</p>
<p>Test: ${App.View.escString(test)}</p>

<!-- Test complex mixed declarations -->

<p>Chart Data: ${App.View.escString(App.Helper.json_encode(chartData))}</p>
<p>Config: ${App.View.escString(App.Helper.json_encode(config))}</p>
<p>Chart State: ${App.View.escString(App.Helper.json_encode(chartState))}</p>


<!-- Test object destructuring with complex data -->

<p>Title: ${App.View.escString(title)}</p>
<p>Description: ${App.View.escString(description)}</p>
<p>Data: ${App.View.escString(App.Helper.json_encode(data))}</p>

<!-- Test no assignment -->

<p>Unassigned: ${App.View.escString(unassigned)}</p>
</div>`, 'html')}`;
            } catch(e) {
                __outputRenderedContent__ = this.__showError(e.message);
                console.warn(e);
            }
            return this.__extends('layouts.base');
            },
        virtualRender: function() {

    let __outputRenderedContent__ = '';
    try {
        __outputRenderedContent__ = `

${this.__sectionScan('document.body', `<div class="container">
<h1>Test @let and @const Directives</h1>

<!-- Test simple variables -->


<p>User: ${App.View.escString(App.Helper.json_encode(user))}</p>
<p>IP: ${App.View.escString(ipAddress)}</p>

<!-- Test array destructuring -->

<p>User State: ${App.View.escString(App.Helper.json_encode(userState))}</p>


<!-- Test object destructuring -->

<p>Name: ${App.View.escString(name)}</p>
<p>Email: ${App.View.escString(email)}</p>
<p>Test: ${App.View.escString(test)}</p>

<!-- Test complex mixed declarations -->

<p>Chart Data: ${App.View.escString(App.Helper.json_encode(chartData))}</p>
<p>Config: ${App.View.escString(App.Helper.json_encode(config))}</p>
<p>Chart State: ${App.View.escString(App.Helper.json_encode(chartState))}</p>


<!-- Test object destructuring with complex data -->

<p>Title: ${App.View.escString(title)}</p>
<p>Description: ${App.View.escString(description)}</p>
<p>Data: ${App.View.escString(App.Helper.json_encode(data))}</p>

<!-- Test no assignment -->

<p>Unassigned: ${App.View.escString(unassigned)}</p>
</div>`, 'html')}`;
    } catch(e) {
        __outputRenderedContent__ = this.__showErrorScan(e.message);
        console.warn(e);
    }
    return this.__extendsScan('layouts.base');
},
        init: function() {  },
        destroy: function() {},
        addCSS: function() {
            // No CSS content found in @onInit
        },
        removeCSS: function() {
            const styles = document.querySelectorAll('style[data-view-name="custom.test"]');
            styles.forEach(style => style.remove());
        }

    });
    return self;
        }